<template>
</template>
<script>
export default {
  sockets: {
    connect(state) {
      console.log("Connected!")
      this.$store.commit("SOCKET_CONNECT")
      var s = this.$socket.emit('pinger', {})
      console.log("PING: " + s.connected)
    },
    disconnect(state) {
      console.log("DISConnected!")

      this.$store.commit("SOCKET_DISCONNECT")
    },

    consoleLog(data) {
      console.log(data)
    },
    ponger(data) {
      console.log("Pong")
      console.log(JSON.stringify(data))
      console.log(data)

      var now = data['last_update']
      var last = this.$store.getters.last_update
      this.$store.commit('last_update', now)

      console.log(now + '==' + last)

      if (now != last) {
        console.log("updating...")
        this.$store.dispatch("RESET_TASK_STORE", {})
        this.$store.dispatch("RESET_BOT_STORE", {})
        this.$socket.emit('getall', {})
      }
      else {
        console.log("up to date")
      }
    },
    SET_SLAVE(data) {
      this.$store.dispatch('SET_SLAVE', data)
    },
    SET_SLAVES(data) {
      for(var i=0; i<data.length; i++) {
        this.$store.dispatch("SET_SLAVE", data[i])
      }
    },
    SET_SLAVE_TYPES(data) {
      for(var i=0; i<data.length; i++) {
        this.$store.dispatch("SET_SLAVE_TYPE", data[i])
      }
    },
    SET_JOB(data) {
      this.$store.dispatch('SET_JOB', data)
    },
    SET_TASK(data) {
      this.$store.dispatch("SET_TASK", data)

    },
    SET_TASKS(data) {
      for(var i=0; i<data.length; i++) {
        this.$store.dispatch("SET_TASK", data[i])
      }

    },
    SET_TASK_GROUP(data) {
      this.$store.dispatch("SET_TASK_GROUP", data)

    }
  }
}
</script>

