import Vue from 'vue'

const defaultState = {
  slaves_map: {},
  slave_types: {}
}

const getters = {
  slaves: (state) => {
    return Object.keys(state.slaves_map).map(slave_id => state.slaves_map[slave_id])
  }
}
const actions = {

  SET_SLAVE: (context,  data) => {
    console.log("SOCKET SET_SLAVE: " + JSON.stringify(data))
    context.commit("SET_SLAVE",data)
  },
  SET_SLAVE_TYPE: (context, data) => {
    console.log("SOCKET SET_SLAVE_TYPE: " + JSON.stringify(data))
    context.commit("SET_SLAVE_TYPE", data)
  },
  RESET_BOT_STORE: (context, data) => {
    console.log("RESET_BOT_STORE")
    context.commit("RESET_BOT_STORE", data)
  }
}

const mutations = {
  RESET_BOT_STORE (state, data) {
    state.slaves_map = {} 
  },
  SET_SLAVE_TYPE (state, data) {
    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
    Vue.set(state.slave_types, d['id'].toString(), d) 
  },
  SET_SLAVE(state, data) {
    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
    var name = state.slave_types[d['slave_type_id'].toString()]['name']
    if (!name) name = 'Unknown'
    d['slave_type'] = name
    Vue.set(state.slaves_map, d['id'].toString(), d) 
  }
}

export default {
  state: defaultState,
  mutations,
  actions,
  getters
}
