import Vue from 'vue'

const defaultState = {
  cjob_id: null,
  task_groups_map: {},
  tasks_map: {},
  jobs_map: {},
}

const getters = {
  task_errors (state, getters) {
    return getters.tasks.filter(function(task) {
      if (task['state']==2) {
        return true
      }
    });
  },
  task_completed (state, getters) {
    return getters.tasks.filter(function(task) {
      if (task['state']==1) {
        return true
      }
    });

  },
  task_inprogress (state, getters) {
    return getters.tasks.filter(function(task) {
      console.log(JSON.stringify(task))
      if (task['state']==0) {
        return true
      }
    });
  },
  cjob_id (state) {
    return state.cjob_id
  },
  job_tasks (state, getters)  {
    var found_group_ids = []
    return getters.tasks.filter(function(task) {
      console.log('taskjob_id: ' +  task['job_id'])
      console.log('cjob_id: ' +  state.cjob_id)
      if (task['job_id'] == state.cjob_id) {
        console.log('task_id' + task['id'])
        var group_id = task['task_group_id']
        if (group_id) {
          console.log("task_group id" + task['task_group_id'])
          if (found_group_ids.include(group_id))
            console.log('is already in')
            return false
          found_group_ids.push(group_id)
          console.log('addin that b')
          task['name'] =  "Group Task: " + task['name']
        }
        return true
      }
    });
  },
  jobs: (state) => {
    return Object.keys(state.jobs_map).map(job_id => state.jobs_map[job_id])
  },

  tasks: (state) => {
    return Object.keys(state.tasks_map).map(task_id => state.tasks_map[task_id])

  },

  tasks_map: (state) => {
    return state.tasks_map
  },
  jobs_map: (state) => {
    return state.jobs_map
  },

  taskgroups_map: (state) => {
    return state.taskgroups_map
  }
}

const actions = {
  RESET_TASK_STORE: (context, data) => {
    console.log("RESET_TASK_STORE")
    context.commit("RESET_TASK_STORE", data)
  },
  SET_TASK_GROUP: (context, data) => {
    console.log("SOCKET SET_TASK_GROUP ACTION DATA: " + data)
    context.commit("SET_TASK_GROUP", data)
  },
  SET_JOB: (context, data) => {
    console.log("SOCKET SET_JOB ACTION DATA: " + JSON.stringify(data))
    context.commit('SET_JOB', data)
    console.log("AFTER: " + JSON.stringify(context.getters.jobs_map))

  },
  SET_JOBS: (context, data) => {
    console.log("SOCKET SET_JOBS ACTION DATA: " + data)
    context.commit('SET_JOBS', data)
  },

  SET_TASK: (context, data) => {
    console.log("SOCKET SET_TASK ACTION DATA: "+JSON.stringify(data))
    context.commit("SET_TASK", data)
    console.log("AFTER: " + JSON.stringify(context.getters.tasks_map))
  },
  SET_TASKS: (context, data) => {
    console.log("SOCKET SET_TASKS ACTION DATA: "+data)
    context.commit("SET_TASKS", data)
  }

}

const mutations = {
  RESET_TASK_STORE (state, data) {
    state.tasks_map = {}
    state.task_groups_map = {}
    state.jobs_map = {}
    //state.cjob_id = null
  },
  SET_CJOB_ID(state, job_id) {
    state.cjob_id = job_id
  },
  SET_TASK_GROUP(state, data) {
    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
    Vue.set(state.tasks_map, d['id'].toString(), d)
  },

  SET_TASK(state, data) {
    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
    
    var ts = 'N/A'
    var tc = 'N/A'
    if (d['time_started']) {
      var d1 = new Date(d['time_started']+' UTC')
      var hours = parseInt(d1.getHours())
      var mins = parseInt(d1.getMinutes())
      var ampm = (hours >= 12) ? "PM" : "AM";
      ts = hours+':'+mins+ampm

      if (d['time_completed']) {
        d1 = new Date(d['time_completed']+' UTC')
        hours = parseInt(d1.getHours())
        mins = parseInt(d1.getMinutes())
        ampm = (hours >= 12) ? "PM" : "AM";
        tc = hours+':'+mins+ampm
      }
    }

    d['time_started'] = ts
    d['time_completed'] = ts

    if (d['error'] == true) {
        d['status'] = 2
      if (d['completed'])
        d['status'] = 3 // error because of another task error
    }
    else
      d['status'] = d['completed'] ? 1:0
    Vue.set(state.tasks_map, d['id'].toString(), d) 
  },
  SET_JOB(state, data) {
    //state.jobs_map[data['id']] = data

    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
      if (d['error'] == true)
        d['status'] = 2
      else
        d['status'] = d['completed'] ? 1:0

    Vue.set(state.jobs_map, d['id'].toString(), d) 
  }
}

export default {
  state: defaultState,
  mutations,
  actions,
  getters
}
